// Material UI
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Link from '@mui/material/Link';

// Owned
import '../App.css';
import { Request, Messages } from '../../../shared';
import BrowserHelper from '../../../shared/BrowserHelper';
import awsconfig from '../../../Config/aws-exports';
import awsconfigdev from '../../../Config/aws-exports-devtest';

// External
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

export default function ReportProblem(props) {

    const { t } = useTranslation();
    const isMobileSafari = BrowserHelper.isMobileSafari(navigator.userAgent);

    const [version, setVersion] = useState();

    const handleReportProblem = () => {
        props.setIsReportingProblem(true);
    };

    const init = async () => {
        try {
            const result = await new Request(Messages.Generic.GetVersion, {}).sendRequest();
            const version = result.message.payload.version;
            setVersion(version);
        } catch(e) {

        }
    }

    const recordOpenApp = async () => {
        await new Request(Messages.Generic.AppOpen, {}).sendRequest();
    }

    useEffect(() => {
        init();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <div>
            <div className="bottom">
                <div>
                    {!props.isReportingProblem && props.hostname ?
                        <div>
                            <Button size="small" onClick={handleReportProblem}>
                                {t('popup.app.button.report_problem')}
                            </Button>
                            {isMobileSafari === true &&
                                <div>
                                    <Button 
                                        target="_blank" 
                                        component={Link} 
                                        href="superagent://" 
                                        color="primary" 
                                        variant="outlined" 
                                        style={{ marginTop: "10px" }} 
                                        className="footer-button mobile-button" size="small"
                                        onClick={recordOpenApp}
                                    >
                                        {t('popup.open_app.button')}
                                    </Button>
                                </div>
                            }
                        </div>
                        : null
                    }
                </div>
                {version ?
                    <Typography variant='caption'>
                        {t('popup.app.version', { version: version })}
                        {awsconfig.aws_user_pools_id === awsconfigdev.aws_user_pools_id ? " (DEV)" : null}
                    </Typography>
                    :
                    null
                }
            </div>
        </div>
    );
}