// Material UI
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

// Icons
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

// Styles
import { makeStyles } from '@mui/styles';

// Owned
import { Request, Messages } from '../../../shared';
import '../App.css';

// External
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
    root: {
        verticalAlign: "center",
        marginTop: '10px'
    },
    box: {
        margin: 20,
        marginTop: 0,
        textAlign: 'right'
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
    backButtonBox: {
        padding: 5,
        textAlign: 'left',
        marginLeft: 14,
        marginTop: 10
    },
    reasonTextField: {
        width: '100%'
    }
}));

export default function ReportProblem(props) {
    const classes = useStyles();
    const { t } = useTranslation();

    const [optionSelected, setOptionSelected] = React.useState('cookie_popups');
    const [isOtherReason, setIsOtherReason] = useState(false);
    const [sendReportLoading, setSendReportLoading] = useState(false);
    const [reasonDescription, setReasonDescription] = useState('');
    const [sendButtonState, setSendButtonState] = useState(optionSelected !== 'other');

    const updateButtonState = (option, description) => {
        let buttonState = (option !== 'other' || (option === 'other' && description !== ''));

        setSendButtonState(buttonState);
    }

    const handleOtherReasonChange = (event) => {
        setReasonDescription(event.target.value);

        updateButtonState(optionSelected, event.target.value);
    };

    const handleBack = () => {
        props.setIsReportingProblem(false);
    };

    const handleChange = (event) => {
        let optionSelected = event.target.value;

        setOptionSelected(optionSelected);
        setIsOtherReason(optionSelected === 'other');

        updateButtonState(optionSelected, reasonDescription);
    };

    const handleSubmit = async (event) => {
        event.preventDefault();

        let description = optionSelected === 'other' ? reasonDescription : null;

        reportWebsite(optionSelected, description);
    }

    const reportWebsite = async (reason, description) => {
        setSendReportLoading(true);
        const hostname = props.hostname;

        new Request(Messages.Report.ReportWebsite, { hostname, reason, description })
            .sendRequest()
            .then(() => {
                setSendReportLoading(false);
                setReasonDescription('');
                props.setSuccessMessage(t('popup.report.success_message'));
            })
            .catch((e) => {
                setSendReportLoading(false);
                props.setErrorMessage(t('popup.report.error_message'));
            });
    }

    switch (props.isReportingProblem) {
        case true:
            return (
                <div>
                    <Box className={classes.backButtonBox}>
                        <Button variant="contained" color="primary" className="back-button mobile-button" onClick={handleBack}>
                            <ArrowBackIcon />
                        </Button>
                    </Box>
                    <Box className={classes.box}>
                        <form onSubmit={handleSubmit}>
                            <RadioGroup aria-label="problem" name="problem" value={optionSelected} onChange={handleChange}>
                                <FormControlLabel value="cookie_popups" control={<Radio />} label={t('popup.report.radiogroup.option1')} />
                                <FormControlLabel value="something_stop_working" control={<Radio />} label={t('popup.report.radiogroup.option2')} />
                                <FormControlLabel value="other" control={<Radio />} label={t('popup.report.radiogroup.option3')} />
                            </RadioGroup>
                            {isOtherReason ? <TextField className={classes.reasonTextField} autoComplete="off" value={reasonDescription} onChange={handleOtherReasonChange} label={t('popup.report.textfield')} id="outlined-secondary" variant="outlined" rows={1} /> : null}
                            <Button className="send-button mobile-button" variant="contained" disabled={!sendButtonState} color="secondary" type="submit">Send</Button>
                        </form>
                    </Box>
                    <Backdrop className={classes.backdrop} open={sendReportLoading}>
                        <CircularProgress color="inherit" />
                    </Backdrop>
                </div>
            );
        default:
            return (null);
    }

}