// Material UI
import CircularProgress from '@mui/material/CircularProgress';

// Styles
import { createTheme, ThemeProvider } from '@mui/material/styles';

// Owned
import HostStateSwitch from './Components/HostStateSwitch';
import Footer from './Components/Footer';
import NavigationBar from './Components/NavigationBar';
import ContentBody from './Components/ContentBody';
import Alerts from '../Shared/View/Alert';
import { Request, Messages } from '../../shared';
import { Tools } from '../../Tools/Tools';
import ReportProblem from './Components/ReportProblem';
import NoInternetConnection from '../Shared/View/NoInternetConnection';
import GroupValidator from '../Shared/GroupValidator';
import BrowserHelper from '../../shared/BrowserHelper';
import SessionState from './Components/SessionState';

import * as Constants from '../Shared/Constants'
import './App.css';

// External
import React, { useEffect, useState } from 'react';

const theme = createTheme({
  overrides: {
    MuiToolbar: {
      regular: {
        minHeight: 48
      },
    },
  },
  palette: {
    primary: {
      main: "#002F44",
    },
    secondary: {
      main: "#FDC737",
    },
    text: {
      primary: "#57636E"
    }
  },
  typography: {
    fontFamily: [
      'Nirmala'
    ]
  }
});

function App() {
  const [isAdmin, setIsAdmin] = useState(false);
  const [loggedIn, setLoggedIn] = useState(Constants.sessionState.LOADING);
  const [hostname, setHostname] = useState(undefined);
  const [fullHostname, setFullHostname] = useState(undefined);
  const [tabId, setTabId] = useState(null);
  const [isRunning, setIsRunning] = useState(true);
  const [haveInfo, setHaveInfo] = useState(false);
  const [isReportingProblem, setIsReportingProblem] = useState(false);
  const [errorMessage, setErrorMessage] = useState(null);
  const [successMessage, setSuccessMessage] = useState(null);
  const [isOnline, setIsOnline] = useState(window.navigator.onLine);
  const isMobileSafari = BrowserHelper.isMobileSafari(navigator.userAgent);
  const [email, setEmail] = useState("");
  
  const init = () => {  
    setIsOnline(window.navigator.onLine);

    new Request(Messages.Generic.PopupOpen).sendRequest()
    .then(() => {

    })
    .catch(e => {

    });
    
    new Request(Messages.Tab.GetActiveTab).sendRequest()
    .then((result) => {
      const tab = result.message.payload.tab;
      let tabId = tab.id;
      setTabId(tabId);

      const fullHostname = tab.url;
      setFullHostname(fullHostname);

      const hostname = Tools.rootDomainForURL(tab.url);
      setHostname(hostname);

      if (hostname === undefined) {
        return;
      }

      new Request(Messages.Whitelist.HostnameIsBlocked, { hostname })
      .sendRequest()
        .then((isBlockedResults) => {
          const isBlocked = isBlockedResults.message.payload.isBlocked;
          setIsRunning(!isBlocked);
        })
        .catch(e => {
          
        });

    }).catch((e) => {
      
    });

    if (window.navigator.onLine) {
      new Request(Messages.Auth.CheckAuth, {}).sendRequest()
        .then(async (result) => {
          const user = result.message.payload;
          setEmail(user.attributes.email);
          setIsAdmin(GroupValidator.isUserAdmin(user));
          setLoggedIn(Constants.sessionState.LOGGED_IN);
        })
        .catch(() => {
          setLoggedIn(Constants.sessionState.NOT_LOGGED_IN);
        });
    } else {
      setLoggedIn(Constants.sessionState.NOT_LOGGED_IN);
    }
  }

  useEffect(() => {
    init();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  switch (loggedIn) {
    case Constants.sessionState.LOADING:
      return (
        <ThemeProvider theme={theme}>
          <div className="App">
            <CircularProgress />
          </div>
        </ThemeProvider>
      )

    default:
      // EDEK check already done with checkAuth, so this shouldn't be a problem
      return (
        <ThemeProvider theme={theme}>
          <div className="App">
            {!isMobileSafari &&
              <NavigationBar
                hostname={fullHostname}
                setSuccessMessage={setSuccessMessage} />
            }

            {isOnline ?

              !isReportingProblem ?
                <div>

                  <HostStateSwitch
                    hostname={hostname}
                    isRunning={isRunning}
                    setIsRunning={setIsRunning}
                    tabId={tabId}
                    isAdmin={isAdmin} />

                  {isMobileSafari ? 
                    <SessionState loggedIn={loggedIn} email={email}/>
                    :
                    <ContentBody
                      isRunning={isRunning}
                      hostname={hostname}
                      loggedIn={loggedIn}
                      haveInfo={haveInfo}
                      setHaveInfo={setHaveInfo} />
                  }
                </div>
                :
                <ReportProblem
                  hostname={hostname}
                  isReportingProblem={isReportingProblem}
                  setIsReportingProblem={setIsReportingProblem}
                  setErrorMessage={setErrorMessage}
                  setSuccessMessage={setSuccessMessage} />

              : <NoInternetConnection showDivider={true} />}

            <Footer
              isReportingProblem={isReportingProblem}
              setIsReportingProblem={setIsReportingProblem}
              hostname={hostname} 
              setSuccessMessage={setSuccessMessage} />

            <Alerts
              error={errorMessage}
              success={successMessage}
              setErrorMessage={setErrorMessage}
              setSuccessMessage={setSuccessMessage} />
          </div>
        </ThemeProvider>
      )
  }
}

export default App;
