// Material UI
import Switch from '@mui/material/Switch';
import Divider from '@mui/material/Divider';
import ListItemSecondaryAction from '@mui/material/ListItemSecondaryAction';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Button from '@mui/material/Button';

// Icons
import DeleteIcon from '@mui/icons-material/Delete';

// Styles
import { makeStyles } from '@mui/styles';

// Owned
import '../App.css';
import { Request, Messages } from '../../../shared';
import { Tools } from '../../../Tools/Tools';

// External
import React from 'react';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
    deleteButton: {
        margin: theme.spacing(1),
    }
}));

export default function HostStateSwitch(props) {

    const { t } = useTranslation();
    const classes = useStyles();

    const handleDeleteCache = (_) => {
        // TO-DO
    }

    const handleDeleteDEK = async () => {
        try {
            await new Request(Messages.KeyManager.DeleteDEK).sendRequest();
        } catch(e) {

        }
    }

    const handleChange = (event) => {
        const hostname = props.hostname;
        let state = event.target.checked;

        props.setIsRunning(state);
    
        new Request(Messages.Whitelist.UpdateHostnameState, { hostname, state })
        .sendRequest()
        .then(() => {
            chrome.tabs.reload(props.tabId)
        }).catch(e => {
            Tools.debugLog("Error: " );
            Tools.debugLog(e, true);
        })

    };

    switch (props.hostname !== undefined) {
        case true:
            return (
                <div className="isRunning">
                    <List>
                        <ListItem>
                            <ListItemText secondary={t('general.in') + " " + props.hostname} primary={props.isRunning ? t('general.enabled') : t('general.disabled')} />
                            <ListItemSecondaryAction>
                                <Switch edge="end" onChange={handleChange} checked={props.isRunning} color="secondary" />
                            </ListItemSecondaryAction>
                        </ListItem>
                    </List>
                    <Divider />
                    {props.isAdmin ?
                        <div>
                            <Button variant="contained" color="secondary" onClick={handleDeleteCache} className={classes.deleteButton} startIcon={<DeleteIcon />}>
                                Delete Provider Info
                            </Button>
                            <Button variant="contained" color="secondary" onClick={handleDeleteDEK} className={classes.deleteButton} startIcon={<DeleteIcon />}>
                                Delete DEK
                            </Button>
                            <Divider />
                        </div>
                        : null}
                </div>
            );
        default:
            return null;
    }
}