// Material UI
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';
import Divider from '@mui/material/Divider';
import Tooltip from '@mui/material/Tooltip';

// Icons
import SettingsIcon from '@mui/icons-material/Settings';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';

// Owned
import '../App.css';
import { Request, Messages } from '../../../shared';

// External
import React from 'react';
import { useTranslation } from 'react-i18next';

export default function NavigationBar(props) {

    const { t } = useTranslation();

    const openOptions = () => {
        chrome.runtime.openOptionsPage(() => { });
    };

    const deleteCookies = () => {
        new Request(Messages.Cookies.DeleteCookies, { hostname: props.hostname })
        .sendRequest()
        .then(() => {
            props.setSuccessMessage(t('popup.navigation.cookies_deleted'));
        });
    }

    return (
        <div>
            <div className="Bar">
                <Grid container spacing={2}>
                    <Grid item xs={8}>
                        <div className="Bar-logo">
                            <Grid item xs={10}>
                                <Link href="#" onClick={() => { window.open("https://www.super-agent.com") }}>
                                    <img src="/images/logo/popup.svg" alt="Super Agent" />
                                </Link>
                            </Grid>
                        </div>
                    </Grid>
                    <Grid item xs={4}>
                        <div className="Bar-icon">
                            <div style={{ marginRight: "10px", display: 'inline-block' }}>
                                <Tooltip title={t('popup.navigation.delete_cookies')}>
                                    <Link href="#" onClick={() => deleteCookies()}>
                                        <DeleteForeverIcon size="small" />
                                    </Link>
                                </Tooltip>
                            </div>
                            <div style={{ display: 'inline-block' }}>
                                <Tooltip title={t('popup.navigation.settings')}>
                                    <Link href="#" onClick={() => openOptions()}>
                                        <SettingsIcon size="small" />
                                    </Link>
                                </Tooltip>
                            </div>
                        </div>
                    </Grid>
                </Grid>
            </div>
            <div className="Bar-divider">
                <Divider />
            </div>
        </div>
    );
}